.parent {
display: grid;
grid-template-columns: repeat(2, 1fr);
grid-template-rows: 2fr 1fr;
grid-column-gap: 0px;
grid-row-gap: 0px;
}

.div1 { grid-area: 1 / 1 / 3 / 2; }
.div2 { grid-area: 1 / 2 / 3 / 3; }

center {
  text-align: center;
  }
  
a {
  color: #3c5832;
  }

/*All sidebars go in here.*/  
.sidebar-group {
  // position: fixed;
  top: 0;
  margin-top: 25px;
  }
  
/*Code shared by all window-style boxes. To see what different specific sidebar classes look like, check the demo page.*/
.sidebar, .mini-sidebar, .wide-sidebar, .box {
  background: #dff7d5;
  border-radius: 5px;
  border: 1px solid #618a50;
  box-shadow: 2px 2px #3c5832;
  }
  
/*Prevent image overflow.*/
.sidebar img, .mini-sidebar img, .wide-sidebar img, .box img {
  max-width: 100%;
  height:auto; /*Prevent squishing*/
  }
  
.sidebar, .sidebar-box {
  display: inline-block;
  vertical-align: top;
  width: 160px;
  }
  
/*Container for vertical stacks of multiple mini-sidebars*/
.sidebar-box {
  margin-left: 10px;
  }
  
.sidebar {
  width: 160px;
  // margin-left: 25px;
  }
  
.mini-sidebar {
  margin-bottom: 25px;
  }
  
.wide-sidebar {
  // margin-left: 25px;
  // margin-top: 25px;
  width: 340px;
  min-height: 150px;
  }
  
/*Inside/main text of window boxes.*/
.inner {
  padding: 10px;
  padding-bottom: 5px;
  padding-top: 0px;
  }

/*The title bar at the top of window boxes.*/
.title-bar, .box-title-bar {
  background: #7aa469;
  color: white;
  font-weight: bold;
  padding: 5px;
  padding-top: 3px;
  padding-bottom: 3px;
  }
  
/*The title bar of boxes has slightly more padding, since there's more room to space text.*/
.box-title-bar {
  padding: 7px;
  padding-top: 5px;
  padding-bottom: 5px;
  }
  
/*For the symbol in the top right corner.*/
.right {
  float: right;
  margin-right: 5px;
  font-family: "Arial", sans-serif;
  line-height: 12px;
  }
  
/*Round circular image.*/
.icon {
  border: #80aa6f solid 2px;
  border-radius: 50%;
  margin-top: 5px;
  }
  
/*Main body text boxes.*/
.box {
  margin-top: 25px;
  margin-left: 10px;
  width: 320px;
  margin-bottom: 25px;
  }


.inner ul {
  padding-inline-start: 0 !important;
  padding-left: 0 !important;
  margin: 0 0 0.8rem 0;
  list-style: none;
}

.inner .sidebar-links {
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 9px;
  // background: black;
  // color: white;
  width: 100%;
  height: 30px;
  }
  
.button {
  padding: 2px;
  display: inline-block
  }

.drama-img {
  margin-top: 10px;
}


@media (max-width: 768px) {
  .div1 .sidebar {
    display: none;
  }

  .div1 {
    display: flex;
    flex-direction: column;
    // gap: 15px;
    width: 100%;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

  .div1 .sidebar-box,
  .div1 .wide-sidebar {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .div1 .sidebar-box {
    display: flex;
    flex-direction: column;
    // gap: 15px;
  }

  .div2 {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }

  .div2 .box {
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  .div1 .mini-sidebar,
  .div1 .wide-sidebar,
  .div2 .box {
    background: #dff7d5;
    border-radius: 5px;
    border: 1px solid #618a50;
    box-shadow: 2px 2px #3c5832;
  }

  .mini-sidebar {
    margin-bottom: 15px;
  }

  .title-bar,
  .box-title-bar {
    width: 100%;
    padding: 7px;
    background: #7aa469;
    box-sizing: border-box;
    color: white;
    font-weight: bold;
    text-align: center;
    border-bottom: 1px solid #618a50;
  }

  .icon,
  .drama-img img {
    max-width: 100%;
    height: auto;
    display: block;
    margin: 10px auto;
  }

  .parent {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 10px;
    box-sizing: border-box;
  }
}