/* Highlight.js ÈªòËÆ§ÂÆπÂô®Á±ª */
.hljs {
  background-color: inherit !important; 
  background-image: none !important;
  padding: 0; 
}

/* Prism.js ÂÆπÂô®Á±ª */
code[class*="language-"],
